import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  CardGroup,
  Button,
  Row,
  Col,
} from "reactstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import axios from "axios";
import { useNavigate } from "react-router-dom";
import Coursedetail from "./ Coursedetail";
import React, { useEffect, useState } from "react";
import { courseapi } from "../../api/user";
import Api from "../../api";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";

const Course = () => {
  const [apiData, setApiData] = useState("");
  useEffect(() => {
    async function callApi() {
      try{
        const response = await courseapi();
        
        setApiData(response);
      } catch(error){
        console.log(error)
      }
     
    }
    callApi();
  }, []);

  const navigate = useNavigate();

  function showDetail(blg, video_link) {
    navigate("../Coursedetail", { state: blg });
  }

  return (
    <div style={{marginTop:"60px"}}>
      <h4 className="mb-3 mt-3 caliber-font">Courses</h4>
      <Row>
        {apiData &&
          apiData.map((apiDetails, index) => {
            return (
              <Col sm="6" lg="6" xl="3" key={index}>
                <Card  className="Card caliber-font" >
                  {/* <CardImg alt="Card image cap" src={apiDetails.featuredImage} style={{minHeight:"250px"}} /> */}
                  <LazyLoadImage
                  
                    loading="lazy"
                    src={apiDetails.featuredImage}
                    effect="blur"
                    placeholderSrc={apiDetails.featuredImage}
                    style={{
                      width: "100%", // Set the desired width
    height: "250px", // Set the desired height
    objectFit: "cover",
                    }}
                  
                  />
                  <CardBody className="p-2"style={{ height: "100%" }}>
                    <CardTitle className="Card-title caliber-font" tag="h5">{apiDetails.title}</CardTitle>
                    {/* <CardSubtitle>{apiDetails.description}</CardSubtitle> */}
                    {/* <CardText className="mt-3">{props.text}</CardText> */}
                    <div style={{ display: "flex", justifyContent: "center" }}>
                    <Button onClick={() => showDetail(apiDetails)}>
                      Watch
                    </Button>
                    </div>
                  </CardBody>
                </Card>
              </Col>
            );
          })}
      </Row>
    </div>
  );
};

export default Course;
